import React from 'react';
import CartItem from './CartItem';
import {Link} from 'react-router-dom';




const Cart = ( {cart , handleUpdateCartQty,handleEmptyCart , handleAddToCart,handleRemoveFromCart}) =>{
   
  if(!cart.line_items) return 'Loading...'


  const renderCart=()=>{
      return <div >
        <div className="product-grid">
              {
              cart.line_items.map((lineItem)=> (
              <CartItem item={lineItem} onUpdateCartQty={handleUpdateCartQty}  onRemoveFromcart={handleRemoveFromCart} />
                
              )
              )
              
              }
              </div>
        </div>
  }
  
  const renderEmptyCart=()=>{
    return <div>
        <Link  to ="/" >You have 0 Itemes in the Cart.Go back to Shopping</Link>
    </div>
  }

    
  return (
    <div>
  
      <h1>Your Shopping Cart</h1>
      <div >
      { !cart.line_items.length ? renderEmptyCart() : renderCart() }
  </div> 
  <div className="subTotal">
            
            <h3>Subtotal : {cart.subtotal.formatted_with_symbol}</h3>
                        
            <div className="sub-btn">
          <Link className="chkout-btn" to="/">BACK</Link>
             <Link to="/checkout" className="chkout-btn" >CHECKOUT</Link >
            </div>
      </div>
    </div>
  );
}


export default Cart;