import React from 'react';
import { FaCartPlus } from "react-icons/fa";
import { Link } from 'react-router-dom'


const Product =({product , onAddToCart} ) =>{

        return (
            
            <div >

                        
                <div className="card">
                    <ul>
                        <li> <img src= {product.media.source} /></li>
                        <li>
                             <div className="product-name-price">
                                 <div>{product.name} </div>        
                                 <div> {product.price.formatted_with_symbol}</div>      
                             </div></li> 
                      
                        <li className="prod-desc" dangerouslySetInnerHTML ={{__html: product.description}} />
                        <li ><button  className="CartIcon"  onClick={()=> onAddToCart(product.id,1)} ><FaCartPlus className="faCartIcon"/></button></li>
                    </ul>
                   
                </div>
                 
            </div>
           
                
        );

}

export default Product;