import React from 'react';




const CartItem = ({item,onUpdateCartQty,onRemoveFromcart}) =>{




    return (  
        //shopping-cart-item
        <div>
        <div className="card cart" >      
                <ul >
                    <li> <img src={item.media.source}/></li>
                    <li><div>
                    <h3 >{item.name}</h3>
                <p>Total Price : {item.line_total.formatted_with_symbol}</p>
            </div>
            </li>

             <li> 
                 <div className="flex-jc">
                     <div className="qty">Qty :
                 <button  onClick={()=> onUpdateCartQty(item.id,item.quantity -1)} >-</button>
                <h4>{item.quantity}</h4>

                <button onClick={()=> onUpdateCartQty(item.id,item.quantity+1)} >+</button>
            </div>
            <button className='remove-btn' onClick={()=> onRemoveFromcart(item.id)}>REMOVE</button>
                 </div>
                 

             </li>
              
            
            </ul>
            

           
                
        </div>

     
        </div>
    );

}

export default CartItem;