import React from 'react';

const Review =({checkoutToken})=>{
    


    return (
     <div>
        {
            checkoutToken.live.line_items.map((product)=>(
                    <div>
                        <div className="orderList" >
                    <div className="jc-sb"  >
                        <div className="itemName">{product.name}</div>
                        <div className="price">{product.line_total.formatted_with_symbol}</div>

                    </div>
                        <div className="qty" >Quantity : {product.quantity}</div>                        
                </div>
                  
                </div> ))  

        }
          <div className="orderList">
               <div>
                     <div className="chk-subtotal">Sub Total : { checkoutToken.live.subtotal.formatted_with_symbol}</div>
                 </div>         
            </div>

        </div>
    );

}

export default Review;