import React from 'react';

import { FaFacebookSquare, FaTwitterSquare,FaInstagramSquare} from "react-icons/fa";

const Footer =()=>{

    return(
        <div className="footer">
           <div  className="copyright">
           Copyright © 2017 All Rights Reserved by Tenzin Thinlay.
           </div>
           <div className="footer-links">
           <ul >
              <li><a href="https://www.facebook.com/" ><FaFacebookSquare className="social-icons-bg" /></a> </li>
              <li><a href="https://www.facebook.com/" ><FaTwitterSquare className="social-icons-bg" /></a> </li>
              <li><a href="https://www.facebook.com/" ><FaInstagramSquare className="social-icons-bg" /></a> </li>   
            </ul>


           </div>


        </div>   
    );

}

export default Footer;