import React from 'react';
import { CheckOut } from '..';
import Review from './Review';
import {loadStripe} from '@stripe/stripe-js';
import {Elements,CardElement,ElementConsumer, ElementsConsumer} from '@stripe/react-stripe-js';
 

const stripePromise = loadStripe('pk_test_51IZVbZSEoWjVNZ61z34kBI9lTNfZVknrfqPUl5QPil858t10ILwovUGWf6ai422J7a6kejLIU86nDoPHu35nob0400qzI6NgAW',);

const PaymentForm =({shippingData, checkoutToken ,nextStep, backStep, onCaptureCheckout}) =>{
    
   
        const handleSubmit= async (event,elements,stripe)=>{
            event.preventDefault();

    
             
            if(!stripe || !elements) return;

            const cardElement = elements.getElement(CardElement);


            const {error,paymentMethod} = await stripe.createPaymentMethod({type: 'card',card:cardElement});
            
            if(error){
              
                console.log(error);
            }
            else{
                 const orderData = {
                     list_items:checkoutToken.live.line_items,
                   
        customer: { firstname: shippingData.fname, lastname:shippingData.lname, email: shippingData.email },
        shipping: { name: 'International', street: shippingData.address1, town_city: shippingData.city, county_state: shippingData.shippingSubdivision, postal_zip_code: shippingData.zip, country: shippingData.shippingCountry },
        fulfillment: { shipping_method: shippingData.shippingOption },
                        payment: {gateway: 'stripe',stripe:{
                            payment_method_id:paymentMethod.id,
                        }}

                 }


                 onCaptureCheckout(checkoutToken.id,orderData);
                 nextStep();
            }

        }

    return (
        <div>
            
            <div className="flex-colummn">
                <h3>Order Summary</h3>
                <Review checkoutToken={checkoutToken} />

                 <div>
                     <h3>Payment Method</h3>
                     <Elements stripe={stripePromise} >
                         <ElementsConsumer>
                         {
                             ({elements,stripe})=>(
                                 <form  onSubmit={(e)=>handleSubmit(e,elements,stripe)}>
                                     <CardElement className="payment-form" />
                                     <br></br>

                                    <div className="flex-jc">
                                        <button className="chkout-btn" onClick={backStep}>Back</button>
                                        <button  type="submit" className="chkout-btn"> Pay </button >
                                    </div>


                                 </form>
                             )
                         }
                         </ElementsConsumer>

                     </Elements>
                 </div>

               

            </div>
        </div>

    );
}

export default PaymentForm;